import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
export const store = new Vuex.Store({
    state:{
       isLogin:false,
       userInfo:{},
    },
    mutations:{
        setInfo(state,userInfo){
            state.isLogin = true,
            state.userInfo = userInfo
        }
    }
});


