<template>
	<div class="login">
		<div class="title">登录</div>
		<div class="form-controller">
			<input type="" placeholder="请输入你的assec_token" class="login-input" v-model="accesstoken" />
			<span class="login-btn" @click="login">登录</span>
		</div>
	</div>
</template>
<script>
export default {
  data() {
    return {
      accesstoken: ""
    };
  },
  methods: {
    login() {
      let accesstoken = this.$data.accesstoken;
      if (
        accesstoken.trim() === undefined ||
        accesstoken.trim() === null ||
        accesstoken.trim() === ""
      ) {
        alert("assec_token为空，请重新输入");
        return;
      }
      this.axios
        .post("https://cnodejs.org/api/v1/accesstoken",{"accesstoken":accesstoken})
        .then(response => {
		  console.log("hhhhhhh",response.data);
		  if(response.data.success){
			  localStorage.setItem("loginname",response.data.loginname)
			  localStorage.setItem("avatar_url",response.data.avatar_url)
			  localStorage.setItem("id",response.data.id)
			  alert("登陆成功")
			  this.$router.go(-1)
		  }
        }).catch((msg)=>{
		  alert("请输入正确的assec_token")
		  console.log(msg)
		});
    }
  }
};
</script>
<style scoped>
.login {
  padding-top: 60px;
}
.title {
  text-align: center;
  font-size: 22px; /*no*/
  margin-bottom: 180px;
}
.form-controller {
  /*height: 100px;*/
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 40px;
}
.login-input {
  width: 75%;
  height: 50px;
  box-sizing: border-box;
  padding-left: 20px;
  border: 1px solid #eee;
  height: 60px;
}
.login-btn {
  width: 25%;
  text-align: center;
  background: #008de1;
  color: #fff;
  font-size: 16px; /*no*/
  height: 60px;
  line-height: 60px;
}
</style>


